<script>
import { mapGetters } from "vuex";
import myMixin from "@/mixin/mixin";

import MobileHeader from "@/components/common/MobileHeader.vue";

export default {
  name: "ObituaryReg2",
  mixins: [myMixin],
  props: [],
  components: {
    MobileHeader
  },
  data() {
    return {
      modalImage: "",
      imageModal: false,
      menu1: false,

      id: this.$route.query.id ? this.$route.query.id : ""
    };
  },
  computed: {
    ...mapGetters(["getObituaryDetail", "memberInfo", "getloadingState"])
  },
  created() {},
  mounted() {
    const parameter = {
      id: this.id
    };
    this.$store.dispatch("REQUEST_OBITUARY_DETAIL", parameter);
  },
  methods: {
    closeImgModal() {
      this.imageModal = false;
      this.$store.commit("END_DEEMED");
    },
    obituaryViewPicture(obituary) {
      debugger;
      if (
        obituary.obituaryFile !== null &&
        obituary.obituaryFile !== undefined &&
        obituary.obituaryFile.length > 0
      ) {
        this.$store.commit("START_DEEMED");
        this.modalImage = obituary.obituaryFile[0].filePath;
        this.imageModal = true;
      } else {
        alert("영정사진이 존재하지 않습니다.");
      }
    },
    move(path) {
      this.$router.push(path);
    },
    requestObituarySend() {
      debugger;
      if (
        this.getObituaryDetail.obituaryEndDate !== null &&
        this.getObituaryDetail.obituaryEndDate !== undefined &&
        this.getObituaryDetail.obituaryEndDate !== "" &&
        new Date(this.getObituaryDetail.obituaryEndDate) < new Date()
      ) {
        alert("완료된 발인의 부고를 전송할 수 없습니다.");
        return;
      }
      const parameter = {
        id: this.id
      };
      this.$store.dispatch("REQUEST_OBITUARY_SEND", parameter);
    },
    requestObituarySendAllOnly() {
      debugger;
      if (
        this.getObituaryDetail.obituaryEndDate !== null &&
        this.getObituaryDetail.obituaryEndDate !== undefined &&
        this.getObituaryDetail.obituaryEndDate !== "" &&
        new Date(this.getObituaryDetail.obituaryEndDate) < new Date()
      ) {
        alert("완료된 발인의 부고를 전송할 수 없습니다.");
        return;
      }
      const parameter = {
        id: this.id
      };
      this.$store.dispatch("REQUEST_OBITUARY_SEND_ALL_ONLY", parameter);
    },
    requestObituaryOnlySend(id) {
      if (
        this.getObituaryDetail.obituaryEndDate !== null &&
        this.getObituaryDetail.obituaryEndDate !== undefined &&
        this.getObituaryDetail.obituaryEndDate !== "" &&
        new Date(this.getObituaryDetail.obituaryEndDate) < new Date()
      ) {
        alert("완료된 발인의 부고를 전송할 수 없습니다.");
        return;
      }
      const parameter = {
        id: id
      };
      this.$store.dispatch("REQUEST_OBITUARY_ALL_SEND", parameter);
    },
    requestObituaryOnlyReply(id) {
      const parameter = {
        id: id
      };
      this.$store.dispatch("REQUEST_OBITUARY_REPLY_SEND", parameter);
    },
    requestObituaryOnlyMovie(id) {
      if (
        confirm(
          "고객한테 제작 안내 메시지가 발송됩니다. 전송하시겠습니까? (제3자 개인정보 동의로 간주함)"
        )
      ) {
        const parameter = {
          id: id
        };
        this.$store.dispatch("REQUEST_OBITUARY_REPLY_MOVIE", parameter);
      }
    },
    obituaryView(obituary) {
      this.$store.dispatch("REQUEST_ENCODE", obituary.id).then(data => {
        if (data.result) {
          // window.open(
          //   `/obituary?id=${data.str}`,
          //   "부고장",
          //   "width=800,height=1500"
          // );
          window.open(
            `${process.env.VUE_APP_OBITUARY_BASEURL}/obituary?id=${data.str}`,
            "부고장",
            "width=800,height=1500"
          );
        }
      });
    },
    obituaryUpdate(id) {
      this.$router.push({
        path: "/obituaryUpdate",
        query: {
          id: id
        }
      });
    }
  }
};
</script>

